    margin-top: 6%;
    
}


.form1{
    width: 50%;
    margin: auto;
    padding: 0;
}

.fullsearch span{
    color: rgb(0, 1, 73);
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5rem;
    display: inline;
}

.searchbutton button{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5rem;
    background-color: rgb(0, 1, 73);
    color: white;
    border: none;
    border-radius: 4px;
    width: 100%;
    margin: 0;
    padding-left: 10px;
    padding-right: 10px;
}

.searchbutton{
    margin: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;
}

.togglediv {
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin: auto;
    margin-bottom: 1%;
}

.gobutton{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: Auto;
    margin-top: 20px;
}

.gobutton button{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5rem;
    background-color: rgb(56, 121, 56);
    color: white;
    border: none;
    border-radius: 4px;
    width: 100%;
    margin: 0;
    padding-top: 7px;
    padding-bottom: 7px;
    padding-left: 12px;
    padding-right: 12px;
}

.searchblock_cards {
    display: inline-block;
    position: relative;
}

